<template>
  <div class="address">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>提现申请</span>
    </header>
    <div class="bank_type" @click="seeInfo">
        <div class="bank_l">
          <p style="margin-bottom: 0.1rem">{{bankInfo.bank_name}}</p>
          <p>尾号{{bankInfo.bank_number | bankFilter}} 储蓄卡</p>
        </div>
      <div class="bank_r"> > </div>
    </div>
    <div class="site-box">
      <label class="site">
          <span>
          提现金额
          </span>
        <input type="text" placeholder=" 提现金额必须是整数" v-model.trim="money">
      </label>
    </div>
    <footer class="footer_btn" @click="save">
      提现
    </footer>
  </div>
</template>

<script>
  import { Toast,MessageBox, Indicator} from 'mint-ui';
  export default {
    data () {
      return {
        bankInfo:{},
        money: '',
        // 支付密码
        payType: '',

      }
    },
    methods: {
      seeInfo(){
          this.$router.push('/withBankList')
      },
      //判断支付密码是否正确;
      checkPay(){
        let self = this;
        MessageBox.prompt('请输入支付密码', '', {
          inputType: 'password',
          confirmButtonClass: '.sure'
        }).then(({ value, action }) => {
          Indicator.open();
          self.post('home/Withdraw/isPass', {
            password: value
          }).then(res => {
            if (res.data.code === 10000) {
              self.withdrawApply();
            } else {
              Indicator.close();
              Toast({
                message: res.data.info,
                duration: 1000
              });
            }
          })
        }).catch(error => {

        });
      },
      //提现申请
      withdrawApply(){
          this.post('home/Withdraw/setWithdrawInfo', {
            money: this.money,
            bank_id: this.bankInfo.id
          }).then(res => {
            Indicator.close();
            if (res.data.code === 10000){
              this.$router.push('/wallet');
              Toast({
                message: res.data.info,
                duration: 1000
              });
            } else {
              Toast({
                message: res.data.info,
                duration: 1000
              });
            }
          })
      },
      save(){
          let isMoney  = !isNaN(this.money) && this.money > 0 && Number.isInteger(this.money-0);
          if (isMoney) {
            this.checkPay();
          } else {
            Toast({
              message: '提现金额必须是整数',
              duration: 1000
            });
          }
      },
      back(){
        this.$router.go(-1);
      },
    },
    filters:{
      bankFilter(val){
        if (val) {
          return val.substr(val.length-4);
        }
      }
    },
    created(){
      this.post('home/Withdraw/getBankState', {}).then(res => {
        if (res.data.code === 10000) {
            this.bankInfo = res.data.info;
        }
      })
    },
  }
</script>

<style scoped>
  @import "../../../static/header.css";
  .address {
    height: 100vh;
    background-color: #F2F2F2;
  }
  .site {
    padding: 0 0.2rem;
    /*display: block;*/
    height: 0.88rem;
    line-height: 0.88rem;
    background-color: #FFFFFF;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #F2F2F2;
  }
  .site span:first-child {
    display: inline-block;
    font-size: 0.32rem;
    color: #000000;
    width: 1.6rem;
  }
  .site span:last-child, .site input{
    font-size: 0.32rem;
    flex: 1;
    display: inline-block;
    height: 0.8rem;
    text-indent: 0.5rem;
  }
  .site input::placeholder {
    font-size: 0.32rem;
    color: #B2B2B2;
  }
  .code img {
    width: 0.39rem;
  }
  .footer_btn {
    height: 0.88rem;
    background-color: #ff6f00;
    text-align: center;
    line-height: 0.88rem;
    font-size: 0.36rem;
    color: #FFFFFF;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .site section img {
    width: 0.39rem;
  }
  .site section span {
    vertical-align: middle;
  }
  .pay {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .sure {
    color: #ff6f00;
  }
/*//列表*/
  .bank_type {
    padding: 0.2rem  0.2rem;
    background-color: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f2f2f2;
  }
  .bank_l p{
      color: #111111;
      font-size: 0.3rem;
  }
</style>



